import React, { useState, useEffect } from "react";
import BackImage from "./BackImage";
import MainImage from "images/top.jpg";
import classnames from "classnames";
function TopContent({ load }) {
  const [imageLoad, setImageLoad] = useState(false);
  useEffect(() => {
    if (load) {
      setTimeout(() => {
        setImageLoad(true);
      }, 500);
    }
  }, [load]);
  return (
    <div className={classnames("top-container", { load: load })}>
      <div className="top-wrapper">
        <div className="main-image-wrap">
          <BackImage src={MainImage} />

          <div className="typo-graphic">
            {imageLoad && (
              <img
                width={220}
                height={80}
                src={require("../images/title_sub_animation.png")}
                alt="text_animation"
              />
            )}
          </div>
          <div className="tape left" />
          <div className="tape right" />
        </div>
        <div className="info-box-wrapper">
          <div className="name-box-wrapper">
            <span className="name first">í•œìƒí˜„</span>
            <span className="slash" />
            <span className="name second">ì´ì±„í¬</span>
          </div>
          <p className="date">2022ë…„ 1ì›” 22ì¼ í† ìš”ì¼ ì˜¤í›„ 1ì‹œ</p>
          <p className="location">ëŒ€ì „ ì„ ìƒ¤ì¸ì›¨ë”©í™€ 5ì¸µ ê·¸ëœë“œë³¼ë£¸</p>
        </div>
      </div>
    </div>
  );
}

export default TopContent;
